import App from 'next/app'
import fetch from 'isomorphic-unfetch'

function GoldenDoor({ Component, pageProps }) {
	console.log(Component)
	return <Component {...pageProps} />
}

GoldenDoor.getInitialProps = async (appContext) => {
	// calls page's `getInitialProps` and fills `appProps.pageProps`
	const res = await fetch('http://goldendoor-api.narcissundtaurus.com/wp-json/gd/de/layout')
	const json = await res.json()
	const appProps = await App.getInitialProps(appContext);
	return { ...appProps, json }
}

export default GoldenDoor